import Vue from 'vue';
import i18n from '../../../i18n';
import shouldMuteNote from '../../../common/scripts/should-mute-note';
import paging from '../../../common/scripts/paging';
export default Vue.extend({
    i18n: i18n(),
    inject: ['column', 'isScrollTop', 'count'],
    mixins: [
        paging({
            limit: 20,
            onQueueChanged: (self, q) => {
                self.count(q.length);
            },
            onPrepend: (self, note, silent) => {
                // 弾く
                if (shouldMuteNote(self.$store.state.i, self.$store.state.settings, note))
                    return false;
                // タブが非表示またはスクロール位置が最上部ではないならタイトルで通知
                if (document.hidden || !self.isScrollTop()) {
                    self.$store.commit('pushBehindNote', note);
                }
            }
        }),
    ],
    props: {
        pagination: {
            required: true
        },
        extract: {
            required: false
        }
    },
    computed: {
        notes() {
            return this.extract ? this.extract(this.items) : this.items;
        },
        _notes() {
            return this.notes.map(note => {
                const date = new Date(note.createdAt).getDate();
                const month = new Date(note.createdAt).getMonth() + 1;
                note._date = date;
                note._datetext = this.$t('@.month-and-day').replace('{month}', month.toString()).replace('{day}', date.toString());
                return note;
            });
        }
    },
    created() {
        this.column.$on('top', this.onTop);
        this.column.$on('bottom', this.onBottom);
        this.init();
    },
    beforeDestroy() {
        this.column.$off('top', this.onTop);
        this.column.$off('bottom', this.onBottom);
    },
    methods: {
        focus() {
            this.$refs.notes.children[0].focus ? this.$refs.notes.children[0].focus() : this.$refs.notes.$el.children[0].focus();
        },
    }
});
